<template>
    <div class="booking">
        <h1 class="booking_title">
            Booking Date & Guest
        </h1>
        <div class="booking_content">
            <div class="date" :class="{checked: checkDate === true, require: required === true}" @click='displayPeople("d")'>
                <div class="date_content">
                    <i class="material-icons">
                        date_range
                    </i>
                    <input type="text" placeholder="Check-in / Check-out" readonly="readonly" :value="`${checkin}/${checkOut}`" v-if='checkin'>
                    <i class="material-icons">
                        expand_more
                    </i>
                </div>
                <div class="date_from" :class="{display: checkDate === true}">
                    <VueHotelDatepicker
                    @confirm = 'setCheckDate'
                    @reset = 'reset'
                    :minDate = 'toDay'
                    :maxDate = 'maxDay'
                    :disabledDates = 'booking'
                    format= 'YYYY-MM-DD'
                    />
                    <button @click.stop="inputPeople('d')">SAVE</button>
                </div>
            </div>
            <div class="people" :class="{checked: checkPeople === true}" @click='displayPeople("p")'>
                <div class="people_content">
                    <i class="material-icons">
                        people_outline
                    </i>
                    <input type="text" placeholder="Guest" readonly="readonly" :value="`Aduits ${aduitsNum}, Children ${childNum}`">
                    <i class="material-icons">
                        expand_more
                    </i>
                </div>
                <div class="people_from" :class="{display: checkPeople === true}">
                    <div class="aduits">Aduits <span @click="aduitsNum++">+</span><span>{{ aduitsNum }}</span><span @click="aduitsLess">-</span></div>
                    <div class="children">Children <span @click="childNum++">+</span><span>{{ childNum }}</span><span @click="childLess">-</span></div>
                    <button @click.stop="inputPeople('p')">SAVE</button>
                </div>
            </div>
            <div class="booking_information" v-if='price'>
                <div class="night">
                    <p>${{ normalDayPrice }} x {{liveDay}} night</p>
                    <p>${{ price }}</p>
                </div>
                <div class="total_price">
                    <p>Total</p>
                    <p>{{ price }}</p>
                </div>
            </div>
            <button @click='check'>Request a booking</button>
        </div>
    </div>
</template>

<script>

import VueHotelDatepicker from '@northwalker/vue-hotel-datepicker'
export default {
    name: 'booking',
    components: {
        VueHotelDatepicker
    },
    data(){
        return {
            aduitsNum: 1,
            childNum: 0,
            checkPeople: false,
            checkDate: false,
            checkin: null,
            checkOut: null,
            liveDay: null,
            date: [],
            toDay: null,
            maxDay: null,
            booking: [],
            price: null,
        }
    },
    created(){
        this.getDay();
        fetch(`https://challenge.thef2e.com/api/thef2e2019/stage6/room/${this.id}`, {
            headers: {
                'Authorization': 'Bearer QMGvFpEER371ozFQf4p51nudhPPa42QhIdHSy9ELWpDjlu0ynmyRww68Xtce'
            }
        }).then(res=>{
            return res.json();
        }).then(myJson=>{
            myJson.booking.forEach(item=>{
                this.booking.push(item.date)
            })
        })
    },
    computed: {
        id(){
            return this.$route.params.id;
        },
        normalDayPrice() {
            return this.$store.state.fullData.room[0].normalDayPrice;
        },
        required(){
            let required;
            this.date.length === 0 ? required = true : required = false;
            return required;
        }
    },
    methods: {
        displayPeople(item){
            if(item === 'p'){
                this.checkPeople = true;
            }else if(item === 'd'){
                this.checkDate = true;
            }
        },
        inputPeople(item){
            if(item === 'p'){
                this.checkPeople = false;
            }else if(item === 'd'){
                this.checkDate = false;
            }
        },
        getDay(){
            let date = new Date();
            let day = date.getDate();
            let month = date.getMonth() + 1;
            let year = date.getFullYear();
            month < 10 ? month = '0' + month : month;
            day < 10 ? day = '0' + day : day;
            this.toDay = `${year}-${month}-${day}`;
            this.maxDay = `${year}-${(month+3)%12}-${day}`;
        },
        check(){
            if(this.date.length === 0) return;
            let id = this.$route.params.id;
            this.$router.push({name: 'check', params: {id: id}})
            this.$store.commit('setBooking', {
                date: this.date,
                people: this.aduitsNum + this.childNum,
                price: this.price,
                night: this.liveDay
            })
        },
        setCheckDate(date){
            let start = new Date(date.start).getTime();
            let end = new Date(date.end).getTime();
            this.liveDay = (end - start) / 86400000;
            for(let i = 0; i < this.liveDay; i++){
                let nextDay = new Date(start + (86400000 * i));
                let year = nextDay.getFullYear();
                let month = nextDay.getMonth() + 1;
                let day = nextDay.getDate();
                month < 10 ? month = '0' + month : month;
                day < 10 ? day = '0' + day : day;
                this.date.push(`${year}-${month}-${day}`);
            } 
            this.checkin = date.start;
            this.checkOut = date.end;
            this.getPrice()
        },
        reset(){
            this.date = [];
            this.checkin = null;
            this.checkOut = null;
        },
        getPrice(){
            this.price = this.normalDayPrice * this.liveDay;
            // switch(week){
            //     case 'Mon':
            //         week = 1;
            //         break;
            //     case 'Tue':
            //         week = 2;
            //         break;
            //     case 'Wed':
            //         week = 3;
            //         break;
            //     case 'Thu':
            //         week = 4;
            //         break;
            //     case 'Fri':
            //         week = 5;
            //         break;
            //     case 'Sat':
            //         week = 6;
            //         break;
            //     case 'Sun':
            //         week = 7;
            //         break;   
            // }
        },
        aduitsLess(){
            this.aduitsNum > 1 ? this.aduitsNum -- : this.aduitsNum = 1;
        },
        childLess(){
            this.childNum > 0 ? this.childNum -- : this.childNum = 0;
        }
    }
}
</script>


<style scoped>
    .booking {
        width: 298px;
        min-height: 268px;
        position: fixed;
        top: 100px;
        right: 17.15vw;
        border: 1px solid #E9E9E9;
        box-shadow: 0 0 6px rgba(0, 0, 0, 0.15);
        z-index: 5;
        background-color: #FFF;
    }
    .booking_content {
        padding: 0 24px;
    }
    .booking_content>div {
        margin-top: 20px;
    }
    .booking_title {
        width: 100%;
        height: 40px;
        font-size: 1rem;
        background-color: #1D1B1B;
        color: #FFF;
        font-weight: 400;
        text-align: center;
        line-height: 40px;
        margin-bottom: 4px;
    }
    .booking_content>button {
        width: 100%;
        height: 40px;
        border: none;
        outline: none;
        background-color: #FFB163;
        border-radius: 20px;
        color: #FFF;
        font-size: 1rem;
        margin: 26px 0 38px;
        cursor: pointer;
    }
    .people, .date {
        width: 250px;
        height: 40px;
        border: 1px solid #858585;
        border-radius: 2px;
        position: relative;
    }
    .people:hover, .date:hover {
        border: 1px solid #FFB163;
    }
    .people_content>input, .date_content>input {
        height: 38px;
        border: none;
        outline: none;
        vertical-align: middle;
    }
    .people_content>i, .date_content>i{
        vertical-align: middle;
        margin: 0 10px;
        line-height: 36px;
    }
    .date_content>i:nth-of-type(2), .people_content>i:nth-of-type(2){
        float: right;
    }
    .people_from {
        display: none;
        width: 250px;
        height: 120px;
        border: 1px solid #FFB163;
        position: absolute;
        top: 37px;
        left: -1px;
        background-color: #FFF;
        padding: 10px 10px 0;
    }
    .people button, .date button {
        display: block;
        width: 210px;
        height: 30px;
        border: none;
        outline: none;
        background-color: #FFB163;
        color: #FFF;
        margin: auto;
        cursor: pointer;
    }
    .aduits, .children {
        width: 100%;
        height: 20px;
        margin-bottom: 12px;
    }
    .aduits>span:not(:nth-of-type(2)),
    .children>span:not(:nth-of-type(2)){
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: #FFB163;
        color: #FFF;
        cursor: pointer;
    }
    .aduits>span:nth-of-type(2),
    .children>span:nth-of-type(2) {
        margin: 0 16px;
    }
    .aduits>span, .children>span{
        text-align: center;
        line-height: 16px;
        float: right;

    }
    .date_from {
        display: none;
        position: absolute;
        right: -1px;
        width: 500px;
        height: auto;
        border: 1px solid #FFB163;
        background-color: #FFF;
        z-index: 1;
        text-align: center;
    }
    .night {
        border-bottom: 1px solid #C8C8C8;
        padding-bottom: 9px;
        font-size: 0.875rem;
        color: #C8C8C8;
    }
    .night>p, .total_price>p{
        display: inline;
    }
    .night>p:nth-of-type(2), .total_price>p:nth-of-type(2){
        float: right;
    }
    .total_price { 
        margin-top: 13px;
        color: #707070;
    }
    .total_price>P:nth-of-type(2){
        font-size: 1.5rem;
        color: #FFB163;
    }
    .checked {
        border-color: #FFB163;
    }
    .display {
        display: block;
    }
    .require {
        border-color: red;
    }

</style>