<template>
    <div class="content">
        <section class="room_img clearfix" v-if='room.imageUrl'>
            <div class="img_big" :style="{backgroundImage: 'url('+ room.imageUrl[index] +')'}">
                <div class="room_name">{{ room.name }}</div>
                <div class="room_price">${{ room.normalDayPrice }}<span>/up</span></div>
                <div class="img_change">
                    <span :class='{big: itemIndex === index}' @click='changeImg(itemIndex)' v-for='(item, itemIndex) in room.imageUrl' :key='item'></span>
                </div>
            </div>
            <div class="img_small">
                <div :style="{backgroundImage: 'url('+ room.imageUrl[(index+1)%room.imageUrl.length] +')'}"></div>
                <div :style="{backgroundImage: 'url('+ room.imageUrl[(index+2)%room.imageUrl.length] +')'}"></div>
            </div>
            <div class="room_informtion clearfix" >
                <div>
                    <img src="../assets/icon_people.svg" alt="icon">
                    <p>{{ room.descriptionShort.GuestMin }}~{{ room.descriptionShort.GuestMax }} people</p>
                </div>
                <div>
                    <img src="../assets/icon_bed.svg" alt="icon">
                    <p>{{ room.descriptionShort.Bed[0] }}</p>
                </div>
                <div>
                    <img src="../assets/icon_bathroom.svg" alt="icon">
                    <p>{{ room.descriptionShort['Private-Bath'] }} bathroom</p>
                </div>
                <div>
                    <img src="../assets/icon_size.svg" alt="icon">
                    <p>{{ room.descriptionShort.Footage }} m2</p>
                </div>
            </div>
        </section>
        <section class="description">
            <div class="title_name">
                <span></span>
                <h1>DESCRIPTION</h1>
            </div>
            <p>{{ room.description }}</p>
        </section>
        <section class="check" v-if='room.checkInAndOut'>
            <div class="title_name">
                <span></span>
                <h1>CHECK-IN AND CHECK-OUT</h1>
            </div>
            <p>Check In Early: {{ room.checkInAndOut.checkInEarly }} / Check In Late: {{ room.checkInAndOut.checkInLate }} Check Out: {{ room.checkInAndOut.checkOut }}</p>
        </section>
        <section class="amenities clearfix" v-if='room.amenities'>
                <div class="title_name">
                    <span></span>
                    <h1>AMENITIES</h1>
                </div>
            <div>
                <div class="icon" :class='{service: room.amenities["Wi-Fi"] === false}'>
                    <img src="../assets/icon_wifi.svg" alt="icon">
                    <p>Wi-Fi</p>
                </div>
                <div class="icon" :class='{service: room.amenities.Breakfast === false}'>
                    <img src="../assets/icon_food.svg" alt="icon">
                    <p>Breakfast</p>
                </div>
                <div class="icon" :class='{service: room.amenities["Mini-Bar"] === false}'>
                    <img src="../assets/icon_bar.svg" alt="icon">
                    <p>Mini-Bar</p>
                </div>
                <div class="icon" :class='{service: room.amenities["Room-Service"] === false}'>
                    <img src="../assets/icon_service.svg" alt="icon">
                    <p>Room Service</p>
                </div>
                <div class="icon" :class='{service: room.amenities["Television"] === false}'>
                    <img src="../assets/icon_TV.svg" alt="icon">
                    <p>Television</p>
                </div>
                <div class="icon" :class='{service: room.amenities["Air-Conditioner"] === false}'>
                    <img src="../assets/icon_airconditioner.svg" alt="icon">
                    <p>Air-Conditioner</p>
                </div>
                <div class="icon" :class='{service: room.amenities.Refrigerator === false}'>
                    <img src="../assets/icon_fridge.svg" alt="icon">
                    <p>Refrigerator</p>
                </div>
                <div class="icon" :class='{service: room.amenities.Sofa === false}'>
                    <img src="../assets/icon_sofa.svg" alt="icon">
                    <p>Sofa</p>
                </div>
                <div class="icon" :class='{service: room.amenities["Great-View"] === false}'>
                    <img src="../assets/icon_view.svg" alt="icon">
                    <p>Great-View</p>
                </div>
                <div class="icon" :class='{service: room.amenities["Smoke-Free"] === false}'>
                    <img src="../assets/icon_nosmoking.svg" alt="icon">
                    <p>Smoke-Free</p>
                </div>
                <div class="icon" :class='{service: room.amenities["Child-Friendly"] === false}'>
                    <img src="../assets/icon_bear.svg" alt="icon">
                    <p>Child-Friendly</p>
                </div>
                <div class="icon" :class='{service: room.amenities["Pet-Friendly"] === false}'>
                    <img src="../assets/icon_pet.svg" alt="icon">
                    <p>Pet-Friendly</p>
                </div>
            </div>
        </section>
        <section class="recently">
            <div class="title_name">
                <h1>RECENTLY VIEWED ROOMS</h1>
            </div>
            <div class="recently_rooms clearfix" v-if='roomRecently'>
                <div class="room clearfix" @click='roomImformation(item.id)' v-for='item in roomRecently' :key='item.name'>
                    <div class="recently_img" :style="{backgroundImage: 'url('+ item.url +')'}"></div>
                    <div class="recently_information">
                        <div>
                            <p>{{ item.name }}</p>
                            <p>$<span>{{ item.price }}</span>/up</p>
                        </div>
                    </div>
                </div>
            </div> 
        </section>
        <Booking/>
    </div>
</template>

<script>
// @ is an alias to /src
import Booking from '@/components/Booking.vue';
export default {
    name: 'room',
    components: {
        Booking,
    },
    data(){
        return {
            index: 0,
            room: [],
        }
    },
    created(){
        fetch(`https://challenge.thef2e.com/api/thef2e2019/stage6/room/${this.id}`, {
            headers: {
                'Authorization': 'Bearer QMGvFpEER371ozFQf4p51nudhPPa42QhIdHSy9ELWpDjlu0ynmyRww68Xtce'
            }
        }).then(res=>{
            return res.json();
        }).then(myJson=>{
            this.room = myJson.room[0];
        })
    },
    computed: {
        id(){
            return this.$route.params.id;
        },
        roomRecently(){
            return this.$store.state.recently;
        },
    },
    methods: {
        roomImformation(id){
            if(id === this.id){
                window.scrollTo(0, 0);
                return;
            }
            this.$store.dispatch('FULL_DATA_GET', id);
            this.$router.push({name: 'about', params:{id: id}})
            fetch(`https://challenge.thef2e.com/api/thef2e2019/stage6/room/${id}`, {
                headers: {
                    'Authorization': 'Bearer QMGvFpEER371ozFQf4p51nudhPPa42QhIdHSy9ELWpDjlu0ynmyRww68Xtce'
                }
            }).then(res=>{
                return res.json();
            }).then(myJson=>{
                this.room = myJson.room[0];
            })
        },
        changeImg(index){
            this.index = index;
        }
    }
}
</script>

<style scoped>
    .clearfix::after{
        content: '';
        display: block;
        clear: both;
    }
    .content {
        width: 100%;
        padding: 0 17.15%;
    }
    .room_img {
        margin-top: 30px;
    }
    .img_big{
        width: 460px;
        height: 590px;
        background-size: cover;
        background-position: center;
        border: 2px solid #1D1B1B;
        margin: 0 26px 20px 0;
        float: left;
        position: relative;
    }
    .img_small {
        float: left;
    }
    .img_small>div {
        width: 156px;
        height: 156px;
        background-size: cover;
        background-position: center;
        border: 2px solid #1D1B1B;
        margin-bottom: 20px;
    }
    .img_change {
        position: absolute;
        left: 16px;
        bottom: 20px;
    }
    .img_change>span {
        display: inline-block;
        width: 0.625rem;
        height: 0.625rem;
        border-radius: 50%;
        border: 2px solid #1D1B1B;
        cursor: pointer;

    }
    .img_change>span:nth-of-type(2n){
        margin: 0 8px;
    }
    .img_change>.big{
        width: 13px;
        height: 13px;
    }
    .room_informtion {
        width: 550px;
        height: 24px;
    }
    .room_informtion>div{
        float: left;
    }
    .room_informtion>div:not(:nth-last-of-type(1)){
        margin-right: 25px;
    }
    .room_informtion img {
        vertical-align: middle;
    }
    .room_informtion p{
        display: inline;
        margin-left: 7px;
        font-size: 0.875rem;
    }
    .title_name {
        font-family: 'Open Sans Condensed', sans-serif;
        height: 41px;
        display: flex;
        align-items: center;
        margin: 20px 0 10px;
    }
    .title_name>span {
        display: inline-block;
        height: 3px;
        width: 40px;
        background-color: #FFB163;
        margin-right: 15px;
    }
    .title_name>h1 {
        font-size: 1.875rem;
    }
    .description>p {
        width: 43.2vw;
        color: #858585;
    }
    .check>p {
        width: 43.2vw;
        color: #858585;
    }
    .amenities {
        width: 40.27vw;
    }
    .icon {
        width: 33.33%;
        float: left;
        margin: 16px 0 24px;
    }
    .icon>img {
        vertical-align: middle;
    }
    .icon>p{
        display: inline;
    }
    .recently_rooms {
        width: 43.2vw;
        margin-bottom: 95px;
    }
    .room {
        width: 298px;
        height: 100px;
        border: 2px solid #C8C8C8;
        float: left;
        margin: 10px 26px 0 0;
        cursor: pointer;
    }
    .recently_img {
        width: 50%;
        height: 100%;
        background-size: cover;
        background-position: center;
        border-right: 2px solid #C8C8C8;
        float: left;
    }
    .room_name, .room_price {
        font-family: 'Open Sans Condensed', sans-serif;
        position: absolute;
        color: #FFF;
        height: 40px;
        line-height: 40px;
        background-color: #1D1B1B;
        padding: 0 8px;
        margin-bottom: 10px;
        font-size: 1.25rem;
        right: -60px;
    }
    .room_name {
        bottom: 106px;
    }
    .room_price{
        bottom: 56px;
    }
    .room:hover {
        transition: all .1s;
        border: 2px solid #FFB163;
        transform: scale(1.01);
    }
    .room:hover .recently_img{
        transition: border .1s;
        border-right: 2px solid #FFB163;
    }
    .room:hover .recently_information>div{
        color: #FFB163;
    }
    .recently_information {
        font-family: 'Open Sans Condensed', sans-serif;
        width: 50%;
        height: 100%;
        float: left;
    }
    .recently_information::before{
        content: '';
        display: inline-block;
        width: 1px;
        height: 100%;
        vertical-align: middle;
    }
    .recently_information>div{
        display: inline-block;
        vertical-align: middle;
        margin-left: 10px;
        font-size: 0.875rem;
        color: #858585;
    }
    .recently_information>div span{
        font-size: 1.25rem;
    }
    .service {
        color: #C8C8C8;
        text-decoration: line-through;
    }
</style>