<template>
    <div class="content clearfix">
        <section class="last_order" v-if='room.imageUrl'>
            <div class="order_img" :style="{backgroundImage: 'url('+room.imageUrl[0]+')'}"></div>
            <div class="order_content">
                <div class="room_name">
                    <h1>{{ room.name }}</h1>
                    <p>Total<span>{{ order[order.length-1].price }}</span></p>
                </div>
                <div class="room_day">
                    <p>{{ order[order.length-1].people }} Guest</p>
                    <p>${{ room.normalDayPrice }} x {{ order[order.length-1].night }} night</p>
                </div>
                <h2>CHECK-IN</h2>
                <div class="form_date">
                    <p>2019/09/19 Thr.</p>
                    <p>2019/09/19 Thr.</p>
                </div>
                <div class="date_time">
                    <p>{{ room.checkInAndOut.checkInEarly }}</p>
                    <p>{{ room.checkInAndOut.checkOut }}</p>
                </div>
            </div>
        </section>
        <form class="traveler">
            <h1>TRAVELER INFORMATION</h1>
            <div class="traveler_price">
                <p>Total</p>
                <input type="text" :value="`$${order[order.length-1].price}`" readonly>
            </div>
            <div class="traveler_name">
                <p>Full Name</p>
                <input type="text" v-model="name" :class="{require: requiredName === true}">
            </div>
            <div class="traveler_phone">
                <p>Phone Number</p>
                <input type="text" v-model="phone" :class="{require: requiredPhone === true}">
            </div>
            <button class="btn" @click="booking">BOOKING</button>
        </form>
        <div class="thank" v-if='thank'>
            <div class="success">
                <img src="../assets/icon_check.svg" alt="check">
                <p>You successfully created your booking!</p>
                <button @click='complete'>OK</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'information',
    data(){
        return {
            room: [],
            phone: '',
            name: '',
            thank: false,
        }
    },
    created(){
        fetch(`https://challenge.thef2e.com/api/thef2e2019/stage6/room/${this.id}`, {
            headers: {
                'Authorization': 'Bearer QMGvFpEER371ozFQf4p51nudhPPa42QhIdHSy9ELWpDjlu0ynmyRww68Xtce'
            }
        }).then(res=>{
            return res.json()
        }).then(myJson=>{
            this.room = myJson.room[0];
        })
    },
    computed: {
        id(){
            return this.$route.params.id;
        },
        // roomData(){
        //     return this.$store.state.fullData.room[0];
        // },
        order(){
            return this.$store.state.booking;
        },
        requiredPhone(){
            let require;
            this.phone.length === 10 ? require = false : require = true;
            return require;
        },
        requiredName(){
            let require;
            this.name.length >= 1 ? require = false : require = true;
            return require;
        }
    },
    methods: {
        booking(){
            if(this.requiredPhone === true || this.requiredName === true) return;
            let id = this.$route.params.id;
            this.$store.dispatch('BOOKING', {
                id: id,
                name: this.name,
                tel: this.phone,
            }).then(()=>{
                this.thank = true;
            })
        },
        complete(){
            this.$router.push({name: 'home'});
        }
    }
}
</script>

<style scoped>
    .clearfix::after{
        content: '';
        display: block;
        clear: both;
    }
    .content {
        width: 100%;
        padding: 0 17.15%;
    }
    .last_order{
        width: 460px;
        height: 650px;
        border: 2px solid #1D1B1B;
        margin: 30px 26px 100px 0;
        float: left;
    }
    .order_img {
        width: 100%;
        height: 450px;
        background-size: cover;
        background-position: center;
    }
    .order_content {
        width: 100%;
        height: 200px;
        border-top: 2px solid #1D1B1B;
        padding: 15px 23px 0 13px;
    }
    .order_content>h2{
        margin-bottom: 9px;
        font-size: 1rem;
        font-weight: 500;
    }
    .room_day {
        font-size: 0.875rem;
        color: #858585;
        margin-bottom: 17px;
    }
    .room_name {
        margin-bottom: 9px;
    }
    .room_name>h1 {
        display: inline;
        font-size: 1.125rem;
        margin-bottom: 9px;
        font-weight: 500;
    }
    .room_name>p {
        float: right;
        font-weight: 500;
    }
    .room_name span {
        color: #FFB163;
        font-size: 1.5rem;
        margin-left: 10px;
        vertical-align: middle;
    }
    .form_date>p:nth-of-type(1){
        display: inline;
    }
    .form_date>p:nth-of-type(2){
        float: right;
    }
    .date_time {
        color: #858585;
    }
    .date_time>p:nth-of-type(1){
        display: inline;
        font-weight: 500;
    }
    .date_time>p:nth-of-type(2){
        float: right;
        font-size: 0.875rem;
    }
    .traveler{
        float: left;
        margin-top: 30px;
    }
    .traveler>h1 {
        margin-bottom: 16px;
    }
    .traveler p {
        display: inline;
        line-height: 42px;
    }
    .traveler input {
        width: 298px;
        height: 42px;
        border: 1px solid #858585;
        border-radius: 2px;
        outline: none;
        padding: 0 10px;
        float: right;
    }
    .traveler_price, .traveler_name, .traveler_phone {
        width: 31.2vw;
        height: 42px;
        margin-bottom: 30px;
    }
    .btn {
        width: 250px;
        height: 40px;
        background-color: #FFB163;
        color: #FFF;
        border: none;
        outline: none;
        border-radius: 20px;
        margin: 19px 24px 0 0;
        cursor: pointer;
        float: right;
    }
    .thank{
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.7);
    }
    .success {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 395px;
        height: 170px;
        background-color: #FFF;
        border-radius: 10px;
        text-align: center;
    }
    .success>img {
        position: absolute;
        top: -40px;
        left: 50%;
        transform: translateX(-50%);
    }
    .success>p {
        font-family: 'Open Sans Condensed', sans-serif;
        font-size: 1.375rem;
        margin: 60px 0 20px;
    }
    .success>button {
        width: 100px;
        height: 40px;
        border-radius: 20px;
        background-color: #FFB163;
        border: none;
        outline: none;
        color: #FFF;
        cursor: pointer;
    }
    .traveler .require {
        border-color: red;
    }
</style>